.anime-card {
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 10px;
    text-align: center;
    transition: transform 0.2s;
  }
  
  .anime-card:hover {
    transform: scale(1.05);
  }
  
  .anime-card img {
    width: 100%;
    height: auto;
    border-radius: 4px;
  }
  
  .anime-card h3 {
    margin-top: 10px;
    font-size: 1.1em;
    color: #333;
  }