.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 25px;
    color: white;
  }

  .header h1 {
    color: #aaa;
    font-size: 36px;
    font-weight: 400;
  }

  .header strong{
    color: #313131;
  }
  
  .header form {
    display: flex;
  }
  
  .header input {
    padding: 8px;
    height: 45px;
    width: 450px;
    font-size: 16px;
    border-radius: 25px;
  }