body {
  margin: 0;
  font-family: 'Fira Sans', sans-serif;
  background-color: #f5f5f5;
}

a {
  color: inherit;
  text-decoration: none;
}

p {
  font-size: 1em;
  color: #555;
}

.app-layout {
  display: flex;
  flex-direction: column;
}

.content-layout {
  display: flex;
}

.main-content {
  flex: 1;
  padding: 20px;
}

.sidebar {
  flex-shrink: 0;
  width: 250px;
  background-color: #f4f4f4;
}

.header {
  background-color: #007bff;
  color: white;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
